@use 'variables' as v;

html {
    font-size: 62.5%;
    box-sizing: border-box;
    scroll-padding-top: 0rem;
}
*, 
*:before,
*:after {
    box-sizing: inherit;
}
body {
    font-family: v.$main-font;
    font-size: 1rem;

    &.fijar-body {
        overflow: hidden;
    }
    &.body-scroll {
        padding-top: 8.6rem;
    }
}
.container {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}
p {
    font-size: 2rem;
    color: v.$black;
}
a {
    text-decoration: none;
}
h1, h2, h3 {
    margin: 0;
}
h1 {
    font-size: 4rem;
}
h2 {
    font-size: 3.5rem;
}
h3 {
    font-size: 3rem;
}
img {
    width: 300px;
    height: 300px;
}
